.footer {
    display: flex;
    justify-content: space-between;
    background-color: black;
    width: 100%;
    height: 100px;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
}

.footer-right {
    color: white;
    text-align: left;
    margin-left: 20px;

}

.footer-links {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 10px;
    margin-right: 60px;
    color: white;
    cursor: pointer;
}